import '../css/App.css';
import React, { useState, useRef}from'react';
import { useNavigate } from 'react-router-dom'
import reportUtils from '../utilities/reportutils.js'
import PathDisplayBlock from '../components/reportPage/recommendations/pathsdisplayblocks.js';
import Investigations from '../components/reportPage/investigations/investigations.js';
import { Grid, Typography, Button } from  '@mui/material';
import SymptomBox from '../components/reportPage/symptoms/symptomBox';
import { SymptomDetailsContext } from '../utilities/context.js'


function Report(props)
{
 const [extraSymptomDetails, setExtraSymptomDetails] = useState([]) 
 const [formData, setFormData]=useState((props.formData))
 const modifiedFormData=useRef()
 const navigate = useNavigate();

 modifiedFormData.current=reportUtils.ValidateTwr(formData)

  let pathBlocks=modifiedFormData.current.sortedPaths.map((eachPath,index)=><PathDisplayBlock eachPath={eachPath} modifiedFormData={modifiedFormData.current} key={index}/>)

  let investigationBlock=<Investigations formData={formData}/>

  console.log(extraSymptomDetails)
  
  const showOutcome = () => {
     navigate('../Outcome')
    }

   return(
     <SymptomDetailsContext.Provider value = {{ extraSymptomDetails, setExtraSymptomDetails}}>
   
   <Grid container >
   
    <Grid item alignSelf="auto" height="fit-content" position="relative" xs={12} sm={12} md={9} key={props.key} sx={{borderRight:"black solid"}} >

   <Grid container >
    <Grid item xs={12} sm={12} md={12}>
       <Typography variant='h4' align="center" sx={{ p:1 }}>  NICE Recommendations</Typography>
       </Grid>
     {pathBlocks}
   </Grid>
   </Grid>
   <Grid item  xs={12} sm={12} md={3}>
   <Grid container> 
   <Grid item xs={12} sm={12} md={12}sx={{borderBottom:"black solid"}} >

   <Typography variant="h4" sx={{ p:1 }}> Symptoms </Typography>
  <SymptomBox formData={props.formData} melanomaSevenPoint={props.melanomaSevenPoint} idaValues={props.idaValues}/>
  </Grid>
   <Grid item xs={12} sm={12} md={12}>
    <Typography variant='h4'  sx={{ p:1 }}> Recommended Investigations</Typography>
  
     {investigationBlock}
  </Grid>
  </Grid>
</Grid>
<Grid>
<Button size='large' variant='contained' onClick={showOutcome}> Finished</Button>
</Grid>
  </Grid>
  
  </SymptomDetailsContext.Provider>
)
}

export default Report
