import React, {useState, useEffect, useContext} from "react";
import { Box, Paper, Typography, Alert, Button } from '@mui/material';
import "../../../css/report.css"
import SymptomItem from "./symptomItem";
import { SymptomDetailsContext } from '../../../utilities/context.js'
import StoryBox from "./symptom story/storyBox";

//WARNING Rerender does not trigger if there is only a single symtom so story does not update - leave for now since needs re-write of context management anyway!!//

function SymptomBox(props){
const[modalHasRun,setModalHasRun]=useState(false)
const [indexReturning,setIndexReturning]=useState() //picks up index of symptom item with modal just closed therfore triggering change of state to force re-render of StoryBox- however see note above//
const {extraSymptomDetails, setExtraSymptomDetails} = useContext(SymptomDetailsContext) 
const [symptomContext,setSymptomContext]= useState(extraSymptomDetails)

const symptomItems=props.formData.symptomList.map((symptom, index)=><SymptomItem symptom={symptom} key={index} index={index} setModalHasRun={setModalHasRun} setIndexReturning={setIndexReturning}/>)

const FinishedAdding=()=>{}

return(
    <Paper sx={{padding:"15px", margin:"15px"}}>
      <Alert sx={{ mb: 1 }} severity='warning'>Section under Development</Alert>
    <Typography variant="subtext">
    You have indicated that the patient has the following symptoms.  You may want to provide additional information on some of these using the "Add Info" Buttons next to each symptom.
    </Typography>
    <Box>
      {symptomItems}
    </Box>
   
    <StoryBox modalHasRun={modalHasRun} melanomaSevenPoint={props.melanomaSevenPoint}/> 
    
     <Box>
     <Button size="small" variant='contained' onClick={FinishedAdding}> Finished Adding Symptom Info </Button>
  </Box>
    </Paper>
)

}

export default SymptomBox